import React, { useEffect, useState } from "react";

import "./css/login.css";

function Login() {

  return (
    <>
      <main className='body-scroll d-flex flex-column h-100 justify-content-center'>
        <div className='col-10 col-md-6 col-lg-6 col-xl-2 mx-auto align-self-center text-center py-4'>
          <div className='col-12 text-center mb-auto px-0'>
            <header className='header'>
              <div className='row'>
                <div className='col-auto'></div>
                <div className='col'>
                  <div className='logo-small'>
                    <img src='./assets/img/logo.png' alt='' />
                    <h5 className='logo-color'>NKS</h5>
                  </div>
                </div>
                <div className='col-auto'></div>
              </div>
            </header>
          </div>
          <form className='was-validated needs-validation' novalidate>
            <div className='form-group form-floating mb-3 is-valid'>
              <input
                type='text'
                className='form-control'
                id='email'
                placeholder='Username'
              />
              <label className='form-control-label' for='email'>
                Username
              </label>
            </div>

            <div className='form-group form-floating is-invalid mb-3'>
              <input
                type='password'
                className='form-control'
                id='password'
                placeholder='Password'
              />
              <label className='form-control-label' for='password'>
                Password
              </label>
              <button
                type='button'
                className='text-danger tooltip-btn'
                data-bs-toggle='tooltip'
                data-bs-placement='left'
                title='Enter valid Password'
                id='passworderror'>
                <i className='bi bi-info-circle'></i>
              </button>
            </div>
            <p className='mb-3 text-center-forget-password'>
              <a href='forgot-password.html' className=''>
                Forgot your password?
              </a>
            </p>

            <button
              type='button'
              className='btn btn-sm btn-default w-75 h-50 mb-4 shadow'
              onclick="window.location.replace('index.html');">
              Login
            </button>
          </form>
          <p className='register-button'>
            <a href='signup.html' target='_self'>
              Register <i className='bi bi-arrow-right'></i>
            </a>
          </p>
        </div>
      </main>
    </>
  );
}

export default Login;
