import React from 'react'

import Header from "../../components/layouts/Header";


const componentName = () => {
    return (
        <>
        <header className="header position-fixed">
        <Header
          material={{
            title: "OTP Verification",
            bi_icon: "bi bi-arrow-left",
            link: "/settingPage",
            logoVisible: false,
            iconNotifVisible: false,
            linkNotif: "",
          }}
        />
      </header>
            {/* <!-- Begin page content --> */}
    <main className="body-scroll d-flex flex-column h-100 justify-content-center">
        <div className="container-fluid h-100">
            <di className="row h-100">
            <div className="col-12 col-md-6 col-lg-5 col-xl-3 mx-auto align-self-center text-center py-4">
                <h1 className="mb-4 text-color-theme">Verify OTP</h1>
                <p className="text-muted mb-4">Verify OTP sent to your provided email address</p>

                <div className="form-floating is-valid mb-3">
                    <input type="text" className="form-control" placeholder="Enter OTP" id="otp"/>
                    <label for="otp">Enter OTP</label>
                </div>
                <button type="button" className="btn btn-sm btn-default w-75 h-50 mb-4 shadow"
              onclick="window.location.replace('index.html');">
              VERIFY
            </button>
            </div>
            <div className="col-12 text-center mt-auto">
                <div className="row justify-content-center footer-info">
                    <div className="col-auto text-center">
                        <span className="progressstimer">
                            <img src="./assets/img/progress.png" alt=""/>
                            <span className="timer" id="timer">3:00</span>
                        </span>
                        <br/>
                        <p className="mb-3"><span className="text-muted">Didn't received yet?</span> <a href="">Resend OTP</a>
                        </p>
                    </div>
                </div>
            </div>
            </di>
        </div>
    </main>
        </>
    )
}

export default componentName